:root {
  --colorlink: #060b35;
  --colorhover: #00acdc;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__logolink {
  margin-left: 50px;
  display: flex;
}

.header__navigation {
  margin-right: 50px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  transition: all .3s ease-in-out;
  display: flex;
}

.card__img {
  margin-inline: auto;
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.card__discription {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rewiews {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 10px;
}

.card__sale-box {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-dis {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__discription {
  color: #34568b;
}

.stars {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars .stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.nav__list {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-link {
  text-align: center;
  text-transform: uppercase;
  color: var(--colorlink);
  align-items: center;
  height: 60px;
  margin: 0 10px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.nav__list-link:hover {
  color: var(--colorhover);
}

.is-active {
  color: var(--colorhover);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--colorhover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__list .nav__list-item:last-child > a {
  margin: 0 0 0 10px;
}

.nav__list .nav__list-item:first-child > a {
  margin: 0 10px 0 0;
}
